import returns from '../../common/returns.vue'
import share from'../../common/share.vue'
export default {
	components:{
		returns,
		share
	},
	data() {
		return {
			title: '团详情',
			data:'',
			goods:'',
			users:'',
			order:'',
			share_arr:{},
			endtime:'',
			id:'',
			duration:'',
			second:'',
			show:true
		}
	},
	onBackPress() {
		//监听back键，关闭弹出菜单
		if (nvImageMenu.isVisible()) {
			nvImageMenu.hide()
			nvMask.hide()
			return true
		}
	},
	methods:{
		tips(){










			
			



			
		},
		jump(url){
			uni.navigateTo({
				url:url
			})
		}
	},
	onLoad(options) {
		this.id = options.id 
		this.share_arr.Url = 'http://wx.huanqiutongmall.com/h5/#/pages/subindex/threeindex/group_details?id='+options.id
	},
	onShow() {
		console.log(this.id)
		this.service.entire(this,'get',this.APIconfig.api_root.subhome.threehome.g_TeamsDetail,{
			user_id:this.$store.state.user.id,
			teamid:this.id
		},function(self,res){
			console.log(res)
			self.data = res.data
			self.goods = res.data.goods
			let strShareTitle = res.data.goods.title
			let strShareImageUrl = res.data.goods.thumb









			self.order = res.data.order
			let user = JSON.stringify(res.data.order.users)
				user = JSON.parse(user)
			self.users = user
			console.log(user)
			self.users.length = res.data.goods.groupnum
			self.endtime =  res.data.endtime
			let times = new Date().getTime().toString().substr(0,10)
			
			let second  = res.data.endtime - times
			
			self.timer = setInterval(function(){
				second --
				var days = Math.floor(second / 86400);
				var hours = Math.floor((second % 86400) / 3600);
				var minutes = Math.floor(((second % 86400) % 3600) / 60);
				var seconds = Math.floor(((second % 86400) % 3600) % 60);
				var duration = days + "天" + hours + "小时" + minutes + "分" + seconds + "秒";
				self.duration = duration
				self.second = second
			},1000)
		})
	},
	onHide(){
		clearInterval(this.timer)
	},
	watch:{
		second(news,old){
			if(news < 0){
				
				this.duration = '拼团订单已过期'
				clearInterval(this.timer)
			}
		},
	}
}
